<template>
  <!-- 详情页导航 -->
  <div>
    <navbar>
      <!-- 返回按钮 -->
      <div slot="left" class="title_left" @click="rbtnClick()">
        <aside class="returnBtn"></aside>
      </div>
      <div slot="center" class="title_center">
        <nav
          v-for="(item, index) in list_item"
          :key="index"
          :class="{ active_item: activIndex == index }"
          @click="itemClick(index)">
          {{ item }}
        </nav>
      </div>
    </navbar>
  </div>
</template>

<script>
// 引入导航栏
import navbar from "components/common/navbar/navbar"
export default {
  data() {
    return {
      list_item: ["商品", "参数", "评论", "推荐"],
      activIndex: 0
    }
  },
  components: {
    navbar
  },
  methods: {
    itemClick(index) {
      this.activIndex = index
      this.$emit('itemClick', index)
    },
    // 返回首页
    rbtnClick(){
      this.$router.back()
    }
  }
}
</script>

<style scoped>
.title_center {
  display: flex;
}
.title_center nav {
  flex: 1;
  font-size: 3.7333vw;
  color: #666666;
}
.active_item {
  color: var(--text-height-color) !important;
  transition: color 0.3s linear;
}
.title_left .returnBtn{
  width: 3.4667vw;
  height: 3.4667vw;
  border-left: 0.5333vw solid #666666;
  border-top: 0.5333vw solid #666666;
  transform: rotate(-45deg);
  margin: 4vw 0 0 6.6667vw;
}
</style>