    </div>
    <section class="price">
      <span class="new-price">{{ wholeGoodsAll.newprice }}</span>
      <span class="oldPrice">{{ wholeGoodsAll.oldPrice }}</span>
      <span class="discountDesc" v-show="discount">{{ wholeGoodsAll.discountDesc}}</span>
    </section>
    <section class="info-other">
      <span class="item-info">{{ wholeGoodsAll.columns[0] }}</span>
      <span class="item-info">{{ wholeGoodsAll.columns[1] }}</span>
