        <span class="services-text">{{wholeGoodsAll.services[index-1].name}}</span>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  props: {
    wholeGoodsAll: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed:{
    discount(){
      return this.wholeGoodsAll.discountDesc=='' ? false : true
    }
  }
}
</script>

