<template>
  <!-- 商品展示模块 -->
  <div class="detail_info" v-if="Object.keys(detailInfoAll).length !== 0">
    <div class="text-info">
      <p>{{ detailInfoAll.desc }}</p>
    </div>
    <div class="key">
      <p>{{ detailInfoAll.detailImage[0].key }}~</p>
    </div>
