        return {}
      }
    }
  },
  data(){
    return {
      // 保存图片片的数量
      imgLength:0,
      // 计数器
      count:0
    }
  },
  // watch:{
  //   // 监听每一张图片的变化
  //   imgInfo(){
  //     this.imgLength=this.detailInfoAll.detailImage[0].list.length;
  //   }
  // },
  methods:{
    // 只要监听的图片每变化一次就从新计算一次高度
    // imgLoad(){
    //   if(++this.count == this.imgLength){
    //      this.$emit('imgLoad');
    //   }
    // }
    imgLoad(){
      this.$emit('imgLoad')
    }
  }
}
</script>

