<template>
  <!-- 用户评价模块 -->
  <div class="evaluate" v-if="Object.keys(useEval).length !== 0">
    <div class="users">
      <div class="evalu">
        <p>用户评价</p>
        <p>更多</p>
      </div>
      <div class="user-text-img">
        <div class="use-imgages">
          <img :src="useEval.userObj.avatar" alt="" />
        </div>
        <div class="use-title">
          <span>{{ useEval.userObj.uname }}</span>
        </div>
      </div>
      <div class="use-content">
        <p>{{ useEval.content }}</p>
        <span class="date">{{ useEval.created | showDate }}</span>
          &nbsp;
        <span class="attribute">{{ useEval.style }}</span>
      </div>
      <div class="use-img">
        <div class="u-img" v-for="(item, index) in useEval.images" :key="index">
          <img :src="item" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { formatDate } from "tools/Date"
export default {
  props: {
    useEval: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  filters: {
    showDate(value) {
      let newTime = new Date(value * 1000)
      return formatDate(newTime, "yyyy-MM-dd")
    }
  }
}
</script>

