<template>
  <!-- 单独抽离出的轮播图 -->
  <div>
    <slot name="swiper"></slot>
  </div>
</template>

<script>
import Swiper from "swiper";
export default {
  mounted() {
    clearTimeout(time);
    let time = setTimeout(() => {
      new Swiper(".swiper-container", {
        nitialSlide: 0,
        autoplay: true,
        autoplay: {
          delay: 4000,
          disableOnInteraction: false
        },
        observer: true, // 启动动态检查器(OB/观众/观看者)
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        spaceBetween: 0,
        loop: true,
        centeredSlides: true,
        speed: 300,
        slidesPerView: 1
      })
    }, 300)
  }
}
</script>

<style scoped>
/* @import "../../../assets/css/swiper.min.css"; */
.swiper-container {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}
.swiper-slide {
  text-align: center;
  font-size: 4.8vw;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.swiper-pagination-bullet {
  background-color:#fff !important;
}
.swiper-slide img {
  width: 100%;
}
</style>