<template>
  <!-- 轮播图模块 -->
  <common-swiper class="banners">
      <div slot="swiper" >
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(item, index) in topImg" :key="index">
                <img :src="item" alt=""/>
            </div>
          </div>
          <!-- 3dd Pagination -->
          <div class="swiper-pagination swposition" v-show="show"></div>
        </div>
      </div>
  </common-swiper>
</template>

<script>
import commonSwiper from "components/common/swiper/commonSwiper"

export default {
  props:{
    topImg:{
      type:Array,
      default(){
        return []
      }
    }
  },
  data(){
    return {
      Pagination:true
    }
  },
  components:{
    commonSwiper
  },
  computed:{
    show(){
      return this.topImg.length==1 ? false : true
    }
  }
}
</script>

<style scoped>
.banners{
  height: 80vw !important;
  overflow-y: hidden !important;
}
.banners .swiper-container{
  position: relative !important;
}
.banners .swposition{
  position: absolute !important;
  top: 73.3333vw !important;
  left: 50% !important;
  transform: translateX(-50%) !important;
}
</style>