<template>
  <!-- 详情页 -->
  <div class="detail">
    <!-- 导航 -->
    <childdatails
      class="navdetail"
      @itemClick="itemnavClick"
      ref="nav"></childdatails>
    <scroll
      class="scrollContents"
      ref="scroll"
      @scroll="contentScroll"
      :probetype="3"
      @top="topshow">
      <!-- 轮播 -->
      <swiperdetails :topImg="topImg"></swiperdetails>
      <!-- 商品信息 -->
      <details-goods :wholeGoodsAll="wholeGoods"></details-goods>
      <!-- 商家信息 -->
      <detail-shop :shopAll="shop"></detail-shop>
      <!-- 服装展示 -->
      <detail-info :detailInfoAll="detailInfo" @imgLoad="imgload"></detail-info>
      <!-- 商品参数展示 -->
      <commodity :commodity="commodityParams" ref="parames"></commodity>
      <!-- 评价 -->
      <evaluate :useEval="userEvaluate" ref="evaluate"></evaluate>
      <!-- 推荐商品 -->
      <recommend-text ref="recommend"></recommend-text>
      <!-- 复用商品模块 -->
      <goods :goodsall="recommends"></goods>
    </scroll>
    <!-- 底部工具栏 -->
    <bottom-bar @addCart="clickAddcart"></bottom-bar>
    <top @click.native="topclick" v-show="isShow"></top>
  </div>
</template>

<script>
/* 网络请求 */
import {
  getdetailsData,
  Goods,
  Shop,
  itemParams,
  UsaerRate,
  getRecommend
} from "network/details"
/* 详情页子组件*/
import childdatails from "../details/childdetails/childdetails"
import swiperdetails from "../details/childdetails/swiperdetails"
import detailsGoods from "../details/childdetails/detailsGoods"
import detailShop from "../details/childdetails/detailShop"
import detailInfo from "../details/childdetails/detailInfo"
import commodity from "../details/childdetails/commodity"
import evaluate from "../details/childdetails/evaluate"
import recommendText from "../details/childdetails/recommendText"
import bottomBar from "../details/childdetails/bottom-bar"
import top from "components/common/backtop/top"
// 复用商品展示模块
import goods from "components/content/goods/goods"
// 第三方
import scroll from "components/common/scroll/scroll"
// 公具类
import { shake } from "tools/util"
export default {
  name: "Details",
  components: {
    swiperdetails,
    scroll,

    childdatails,
    detailsGoods,
    detailShop,
    detailInfo,
    commodity,
    evaluate,
    recommendText,
    goods,
    bottomBar,
    top
  },
  data() {
    return {
      isShow: false,
      // 保存商品id
      iid: null,
      // 轮播图
      topImg: [],
      // 保存价格和title的对象
      wholeGoods: {},
      // 店铺和商品信息
      shop: {},
      // 商品描述信息和穿着效果数据
      detailInfo: {},
      // 商品参数
      commodityParams: {},
      // 用户评价
      userEvaluate: {},
      // 推荐商品的数据
      recommends: [],
      // 存储导航跳转
      themeJump: [],
      // 存储防抖函数
      getshake: null,
      getshakes: null,
      indexAll: 0,
    }
  },
  created() {
    this.iids()
    this.getdetailsDatas()
    this.getRecommends()
    this.getshake = shake(() => {
      /* 
        created()里面获取不到offsetTop  mounted()图片是异步加载的获取不到正确的offsetTop 
        所以写在这里然后在methods里面调用。
      */
      // 把第一次获取到的清空然后在push进去
        this.themeJump = []
        this.themeJump.push(0)
        this.themeJump.push(this.$refs.parames.$el.offsetTop)
        this.themeJump.push(this.$refs.evaluate.$el.offsetTop)
        this.themeJump.push(this.$refs.recommend.$el.offsetTop)
        this.themeJump.push(Number.MAX_VALUE)
    }, 200)
  },
  methods: {
    // 网络请求
    // 这里做了抽离在created里面调用
    iids() {
      // 保存商品id
      this.iid = this.$route.params.iid
    },
    // 根据id获取相关商品详细信息
    getdetailsDatas() {
      getdetailsData(this.iid).then(res => {
        // res.result传参太长这里存到data
        let data = res.result
        // console.log(data)
        // 轮播图
        this.topImg = data.itemInfo.topImages
        // 商品价格和title
        this.wholeGoods = new Goods(data.itemInfo, data.columns, data.shopInfo)
        // 商家信息
        this.shop = new Shop(data.shopInfo)
        // 描述信息和穿着效果
        this.detailInfo = data.detailInfo
        // 商品参数
        // 这里需要进行判断因为有的商品信息里面有这个数据有的没有
        if(data.itemParams.rule) {
          data.itemParams.rule = data.itemParams.rule
        }else {
          return
        }
        this.commodityParams = new itemParams(
          data.itemParams.info,
          data.itemParams.rule
        )
        // 用户评价
        if (data.rate.cRate !== 0) {
          this.userEvaluate = new UsaerRate(data.rate.list[0])
        }
        res.result = data
      })
    },
    // 推荐商品的展示
    getRecommends() {
      getRecommend().then(res => {
        this.recommends = res.data.list
      })
    },
    // 事件处理
    imgload() {
      // 只要监听的图片每变化一次就调用一次refresh()
      this.getshakes = shake(this.$refs.scroll.refresh, 200)
      this.getshakes()
      this.getshake()
      // console.log("图片变化：🤣😂🤣😒🤦‍♂️🤦‍♂️🙌😁💕😁😂🤣😂👍😉")
    },
    itemnavClick(index) {
      this.$refs.scroll.scroll.scrollTo(0, -this.themeJump[index], 300)
    },
    contentScroll(position) {
      let positionY = -position.y
      for (let i = 0; i < this.themeJump.length-1; i++) {
        // 第一种方法
        // if (
        //   (this.indexAll !== i && i < this.themeJump.length - 1 && positionY >= this.themeJump[i] && positionY < this.themeJump[i + 1])
        //   || (i == this.themeJump.length - 1 && positionY >= this.themeJump[i])
        // ) {
        //   this.indexAll = i;
        //   this.$refs.nav.activIndex = this.indexAll;
        // }

        // 第二种方法
        if(this.indexAll !== i &&(positionY >= this.themeJump[i] && positionY < this.themeJump[i+1])){
          this.indexAll = i
          this.$refs.nav.activIndex = this.indexAll
        }
      }
    },
    topclick() {
      // 返回顶部
      // scrollTo(x,y,缓动)
      this.$refs.scroll && this.$refs.scroll.scroll.scrollTo(0, 0, 300)
    },
    topshow(position) {
      // 当页面滑动到1000时就显示返回顶部按钮否则就隐藏
      this.isShow = Math.abs(position.y) > 1000
    },
    // 购物车
    clickAddcart() {
      let storage = {}
      storage.image = this.topImg[0]
      storage.title = this.wholeGoods.title
      storage.desc = this.wholeGoods.desc
      storage.lowNowPrice = this.wholeGoods.lowNowPrice
      storage.iid = this.iid

      this.$store.dispatch("addCart", storage).then(res => {
        // 调用toast插件里面的函数
        this.$toast.show(res, 1500)
      })
    }
  }
}
</script>

<style scoped>
.detail {
  overflow: hidden;
  position: relative;
  z-index: 9;
  background: #fff;
  height: 100vh;
}
.navdetail {
  position: relative;
  z-index: 99;
  background: #fff;
}
.scrollContents {
  position: absolute;
  top: 11.7333vw;
  bottom: 13.0667vw;
  left: 0;
  right: 0;
}
</style>