<template>
  <!-- 商家模块 -->
  <div class="shopAll" v-if="Object.keys(shopAll).length !== 0">
    <div class="shops">
      <div class="log">
        <img :src="shopAll.shopLogo" alt="" />
      </div>
      <div class="name">
        <p>{{ shopAll.name }}</p>
      </div>
    </div>
    <div class="shopme">
      <div class="details">
        <p class="text">{{ shopAll.sells | showFilters }}</p>
        <span class="sales">总销量</span>
      </div>
      <div class="details">
        <p class="text">{{ shopAll.goods }}</p>
        <span class="sales">全部宝贝</span>
      </div>

      <div class="detailsAll">
        <div class="list-item" v-for="item in shopAll.score" :key="item.index">
          <div class="item-info">
            <span class="item-name">{{ item.name }}</span>
            <span :class="{ actve_color: item.isBetter }" class="score">
              {{item.score}}
            </span>
            <span :class="{ bg_active_color: item.isBetter }" class="better">
              {{item.isBetter ? "高" : "低"}}
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="btn-link">
        <a :href="shopAll.allGoodsUrl">进入店铺</a>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    shopAll: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  filters: {
    // 过滤多余的数字保留一位数
    showFilters(value) {
      if (value < 10000) {
        return value;
      }
      return (value / 10000).toFixed(1) + "万"
    }
  }
}
</script>

<style scoped>
.shopAll {
  padding: 0 1.8667vw;
  border-bottom: 1.0667vw solid #f2f5f8;
}
.shopAll .shops {
  display: flex;
  align-items: center;
  margin: 3.7333vw 0;
}
.shopAll .shops .log {
  overflow: hidden;
  width: 12.7973vw;
  height: 12.7973vw;
  border-radius: 50%;
  border: 1px solid #999;
}
.shopAll .shops .log img {
  width: 100%;
  height: 100%;
}
.shopAll .shops .name p {
  margin-left: 3.2vw;
  font-size: 5.12vw;
  color: #999999;
}
.shopme {
  display: flex;
  height: 25.6vw;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 4vw;
}
.shopme .details {
  font-size: 4.5333vw;
  color: #454545;
  padding: 0 2.4vw;
  text-align: center;
}
.shopme .details .text {
  margin-bottom: 1.0667vw;
}
.shopme .details:nth-child(2) {
  border-right: 0.2667vw solid rgba(102, 102, 102, 0.2);
}
.shopme .details .sales {
  font-size: 3.2vw;
  color: #666666;
}
.shopme .detailsAll span {
  margin-left: 1.8667vw;
  font-size: 3.4667vw;
}
.shopme .detailsAll .item-name {
  color: #666666;
}
.shopme .detailsAll .item-info {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 2.4vw;
}
.shopme .detailsAll .item-info .score {
  color: #ff0000;
}
.shopme .detailsAll .item-info .better {
  background: #ff0000;
  color:#fff;
}
.shopAll .btn-link{
  text-align: center;
  margin-bottom: 4vw;
  font-size: 4.2667vw;
}
.shopAll .btn-link a{
  padding: 1.3333vw 7.4667vw;
  border-radius: 1.3333vw;
  background: #d9d9d9;
  color:#fff;
}
/* 动态添加类 */
.actve_color {
  color: #00b100 !important;
}

.bg_active_color {
  background: #00b100 !important;
}
</style>