import { request } from "network/request"
export function getdetailsData(iid) {
  return request({
    url: '/api/hy66/detail',
    params: {
      iid
    }
  })
}

export function getRecommend(){
  return request({
    url:'/api/hy66/recommend'
  })
}
// 由于数据太乱这里用对象将数据整合起来存储
// 商品数据
export class Goods {
  constructor(itemInfo, columns, shopInfo) {
    // 商品标题
    this.title = itemInfo.title
    // 商品打折价格
    this.newprice = itemInfo.price
    // 商品原价格
    this.oldPrice = itemInfo.oldPrice
    // 背景颜色
    this.discountBgColor = itemInfo.discountBgColor
    // 打折图标
    this.discountDesc = itemInfo.discountDesc
    this.realPrice = itemInfo.lowNowPrice
    // 商品信息
    this.columns = columns
    // 售后服务
    this.services = shopInfo.services
    // 商品描述
    this.desc = itemInfo.desc
    // 最终价格
    this.lowNowPrice = itemInfo.lowNowPrice
  }
}

// 商家数据
export class Shop {
  constructor(shop) {
    // 商家logo
    this.shopLogo = shop.shopLogo
    // 商家名称
    this.name = shop.name
    // 全部销量
    this.sells = shop.cSells
    // 全部商品
    this.goods = shop.cGoods
    // 商品描述信息
    this.score = shop.score
    // 店铺
    this.allGoodsUrl = shop.allGoodsUrl
  }
}

// 尺码和说明
export class itemParams {
  constructor(info, rule) {
    this.images = info.images ? info.images[0] : ''
    // 产品参数
    this.set = info.set
    // 产品尺码
    this.size = rule.tables
    // 产品说明
    this.disclaimer = rule.disclaimer
  }
}

// 评价
export class UsaerRate {
  constructor(rate) {
    // 用户对象
    this.userObj = rate.user
    // 评价内容 
    this.content = rate.content
    // 样式
    this.style = rate.style
    // 时间
    this.created = rate.created
    // 图片
    this.images = rate.images
  }
}